<template>
	<div class="btn-group btn-group-xs" role="group" aria-label="...">
	    <button type="button" class="btn btn-default btn-raised" @click='insert("#")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert(">")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("\n- ")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("\n    ")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("\n***\n")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("[](http://example.com/)")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("*content*")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("**content**")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("`code`")'></button>
	    <button type="button" class="btn btn-default btn-raised" @click='insert("![](example.img)")'></button>
    </div>
</template>

<script>
	export default {
		methods: {
	    insert: function (myValue) {
	      //IE support 
	      var myField = document.getElementById('inputter')
	      if (document.selection) { 
	        myField.focus() 
	        sel = document.selection.createRange() 
	        sel.text = myValue 
	        sel.select() 
	      } 
	      //MOZILLA/NETSCAPE support 
	      else if (myField.selectionStart || myField.selectionStart == '0') { 
	        var startPos = myField.selectionStart 
	        var endPos = myField.selectionEnd 
	        // save scrollTop before insert 
	        var restoreTop = myField.scrollTop 
	        myField.value = myField.value.substring(0, startPos) + myValue + myField.value.substring(endPos,myField.value.length) 
	        if (restoreTop > 0) { 
	          // restore previous scrollTop 
	          myField.scrollTop = restoreTop 
	        } 
	          myField.focus() 
	          myField.selectionStart = startPos + myValue.length 
	          myField.selectionEnd = startPos + myValue.length 
	      } else { 
	          myField.value += myValue 
	          myField.focus() 
	        } 
	    }
	  }
	}
</script>


